// Library Imports
import React, { Component } from "react";
import { connect } from "react-redux";

// Relative Imports
import { Content } from "../styles";
import { Check, Unchecked } from "./styles";
import Header from "../../UI/Header/Centered";
import Aux from "../../../Aux";
import List from "../../UI/List/Detail";
import ListContainer from "../../UI/List/Container";
import P from "../../../_Text/P";

class SettingsDetail extends Component {
  NativeCurrency = () => {
    return this.props.nativeCurrency.options.map(currency => {
      let isNativeCurrency =
        this.props.nativeCurrency.ticker === currency.ticker;
      return (
        <List
          symbol={currency.symbol}
          name={currency.name}
          subname={currency.ticker}
          info={isNativeCurrency ? <Check /> : <Unchecked />}
          onClick={() => this.props.changeNativeCurrency(currency.ticker)}
        />
      );
    });
  };
  render() {
    return (
      <Aux>
        <Header
          H1={"Native Currency"}
          P="Pick a native currency:"
          close={this.props.close}
          back={() => this.props.routing("Overview", "")}
        />
        <Content>
          <P styling={"margin: 0px 0px 10px 0px"}>Native currencies:</P>
          <ListContainer>{this.NativeCurrency()}</ListContainer>
        </Content>
      </Aux>
    );
  }
}

const mapStateToProps = state => ({
  nativeCurrency: state.NativeCurrency
});

export const mapDispatchToProps = dispatch => ({
  changeNativeCurrency: ticker =>
    dispatch({
      type: "NATIVECURRENCY_CHANGE",
      ticker: ticker
    })
});

export default connect(mapStateToProps, mapDispatchToProps)(SettingsDetail);
